@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Inclusive+Sans:ital@0;1&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;


@layer utilities {
    .snap {
      scroll-snap-type: var(--scroll-snap-direction) var(--scroll-snap-constraint);
    }
    .snap-y {
      --scroll-snap-direction: y;
    }
    .snap-mandatory {
      --scroll-snap-constraint: mandatory;
    }
    .snap-start {
      scroll-snap-align: start;
    }
  }